@charset "UTF-8";
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@font-face {
  font-family: "iconfont";
  src: url("../fonts/iconfont.eot");
  src: url("../fonts/iconfont.eot?#iefix") format("eot"), url("../fonts/iconfont.woff2") format("woff2"), url("../fonts/iconfont.woff") format("woff"), url("../fonts/iconfont.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

.icon {
  font-family: "iconfont";
  font-style: normal;
  font-weight: normal;
  text-rendering: auto;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon {
  display: inline-block;
}

.icon-arr:before {
  content: "Ó®Å";
}

.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-loading .slick-list {
  background: white;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-list,
.slick-track,
.slick-slide,
.slick-slide img {
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  zoom: 1;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
}

.slick-slide img {
  display: block;
  max-width: 100%;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-prev,
.slick-next {
  position: absolute;
  top: 50%;
  margin-top: -10px;
  font: 0/0 a;
  text-shadow: none;
  color: transparent;
  height: 20px;
  background: none;
  left: 0;
  z-index: 1;
}

.slick-prev:focus,
.slick-next:focus {
  outline: none;
}

.slick-prev.slick-disabled,
.slick-next.slick-disabled {
  opacity: 0.2;
}

.slick-prev:before,
.slick-next:before {
  height: 30px;
  line-height: 30px;
  content: "prev";
  display: inline-block;
  vertical-align: middle;
  font-size: 15px;
  color: #fff;
  background: #000;
}

.slick-next {
  right: 0;
  left: auto;
}

.slick-next:before {
  content: "next";
  display: inline-block;
}

.slick-dots {
  text-align: center;
  position: absolute;
  margin: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slick-dots li {
  position: relative;
  display: inline-block;
  padding: 0 3px;
}

.slick-dots li.slick-active button {
  background: #000;
}

.slick-dots button {
  display: block;
  width: 10px;
  height: 10px;
  background: #fff;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.25);
  cursor: pointer;
  font: 0/0 a;
  text-shadow: none;
  color: transparent;
  border-radius: 50%;
}

body {
  background: #FFF;
  font-family: Arial, sans-serif;
  font-size: 16px;
  line-height: 1.4;
}

.out {
  width: 600px;
  margin: auto;
  text-align: center;
}

.popup-slider {
  position: relative;
  width: 100%;
  min-height: 425px;
  overflow: hidden;
}

.popup-slider__item {
  min-height: 425px;
  padding: 25px;
  background-color: #fff;
}

.popup-slider__txt {
  width: 50%;
  display: inline-block;
  vertical-align: middle;
}

.popup-slider__img {
  width: 50%;
  display: inline-block;
  vertical-align: middle;
  text-align: center;
}

.popup-slider__img img {
  display: inline-block;
}

.popup-more {
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
  min-width: 100%;
  min-height: 100%;
  z-index: 6000;
  transform: translate(-50%, -50%);
  background-color: rgba(0, 0, 0, 0.7);
  overflow: hidden;
}

.popup-more__in {
  position: relative;
  min-width: 100%;
  min-height: 1500px;
}

.popup-more__content {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 6001;
  transform: translate(-50%, -50%);
  width: 1000px;
  min-height: 425px;
}

.popup-more__close {
  position: relative;
  width: 100%;
  text-align: right;
}

.popup-more__close button {
  position: absolute;
  right: -50px;
  top: -60px;
  background-color: transparent;
  color: white;
}

.popup-more__close button::before {
  content: '\2612';
  font-size: 40px;
}

.is-open {
  display: block;
}

.tabs {
  width: 100%;
  margin-top: 30px;
}

.tabs__item {
  display: inline-block;
  margin: 0 20px;
}

.tabs__btn {
  text-decoration: none;
  text-transform: uppercase;
  font-weight: bold;
  letter-spacing: 2px;
  line-height: 50px;
  padding: 10px 30px;
  border: 2px solid orange;
  color: black;
}

.tabs .is-active {
  background-color: orange;
}

.tab-content {
  width: 100%;
  position: relative;
}

.tab-content h4 {
  font-size: 20px;
}

.tab-content__bl {
  display: none;
  margin-top: -7px;
  padding: 50px 0;
  background-color: orange;
}

.tab-content__slide {
  width: 208px;
  display: inline-block;
  padding: 6px 4px;
  margin: 0 44px;
  background-color: black;
  color: white;
}

.tab-content__btn {
  display: inline-block;
  width: 100%;
  text-transform: uppercase;
  text-decoration: none;
  line-height: 40px;
  font-weight: bolder;
  color: orange;
}

.tab-content .slick-slide img {
  display: inline-block;
}

.tab-content .slick-prev,
.tab-content .slick-next {
  top: 10px;
  left: -40px;
  width: 40px;
  height: 100%;
  text-align: center;
  background-color: transparent;
  font-weight: bold;
}

.tab-content .slick-prev:before,
.tab-content .slick-next:before {
  display: inline-block;
  padding: 0 5px;
  color: orange;
  border: 1px solid black;
}

.tab-content .slick-next {
  left: auto;
  right: -40px;
}

.tab-content .slick-prev:before {
  content: "\27A4";
  transform: rotate(180deg);
}

.tab-content .slick-next:before {
  content: "\27A4";
}
