body {
	background: #FFF;
	font-family: Arial, sans-serif;
	font-size: 16px;
	line-height: 1.4; }

.out {
	width: 600px;
	margin: auto;
	text-align: center; }

.popup-slider {
	position: relative;
	width: 100%;
	min-height: 425px;
	overflow: hidden;
	&__item {
		min-height: 425px;
		padding: 25px;
		background-color: #fff; }
	&__txt {
		width: 50%;
		display: inline-block;
		vertical-align: middle;
		h4 {}
		ul {}
		li {}
		p {}
		span {} }
	&__img {
		width: 50%;
		display: inline-block;
		vertical-align: middle;
		text-align: center;
		img {
			display: inline-block; } } }
